.mob-chevron{
	position: relative;
	width: 2.6rem;
	height: 0.57rem;
	opacity: 1;
	margin-bottom: 0.4rem;
}
.mob-chevron:before{
	content: ' ';
	position: absolute;
	top: 0;
	height: 0.5rem;
	width: 1.3rem;
	background-color: rgba(96,64,137,0.5);
	-webkit-transform: skew(0deg, 30deg);
	    -ms-transform: skew(0deg, 30deg);
	        transform: skew(0deg, 30deg);
	left:0;
	border-top-left-radius: 0.5rem;
    border-bottom-left-radius: 0.2rem;
}
.mob-chevron:after{
	content: ' ';
	position: absolute;
	top: 0;
	height: 0.5rem;
	width: 1.3rem;
	background-color: rgba(96,64,137,0.5);
	right: 0;
	-webkit-transform: skew(0deg, -30deg);
	    -ms-transform: skew(0deg, -30deg);
	        transform: skew(0deg, -30deg);
	border-top-right-radius: 0.5rem;
    border-bottom-right-radius: 0.2rem;
}
.mob-chevron:first-child:before,.mob-chevron:first-child:after {
   background-color: rgba(96,64,137,1);
   -webkit-animation: move 0.7s ease-in-out infinite;
           animation: move 0.7s ease-in-out infinite;
}
.mob-chevron:first-child{
	top:0;
}
@-webkit-keyframes move {
25% {
		top: calc(0.57rem * 1);
}
50% {
		top: calc(0.57rem * 2);
}
75% {
	   top: calc(0.57rem * 3);
}
100% {
		top: calc(0.57rem * 4);
}
}
@keyframes move {
25% {
		top: calc(0.57rem * 1);
}
50% {
		top: calc(0.57rem * 2);
}
75% {
	   top: calc(0.57rem * 3);
}
